<template>
    <div>
        <el-row :gutter="20">
            <el-col :span="14" :offset="5">
                <div class="bg-gray grid-content">
                    <a href="#/">To HomePage</a>
                </div>
            </el-col>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="3" :offset="5">
                <div class="bg-aqua grid-content div-aside">
                    aside
                </div>
            </el-col>
            <el-col :span="11">
                <div class="bg-pink grid-content div-main">
                    main
                </div>
            </el-col>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="14" :offset="5">
                <div class="bg-gray grid-content">
                    footer
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
  name: 'MainPage'
}
</script>

<style scoped>
.bg-gray{
    background-color: rgb(226, 225, 225);
}
.bg-aqua{
    background-color: aqua;
}
.bg-pink{
    background-color: rgb(255, 231, 235);
}
.el-row {
    margin-bottom: 20px;
}
.el-row:last-child {
    margin-bottom: 0;
}
.div-aside,.div-main{
    height: 300px;
}
.grid-content {
    border-radius: 4px;
    min-height: 50px;
}
</style>


